/* Login & Sign in Page */

/* /////Celestial Bodies */

.circle {
  /*Earth*/
  margin-left: 270vh;
  margin-top: 70vh;
  height: 63.5px;
  width: 63.5px;
  position: absolute;
  border-radius: 50%;
  background-image: radial-gradient(circle, rgb(0, 0, 255), rgba(0, 0, 0, 0.87));
  box-shadow: 0px 0px 20px rgb(87, 87, 223);
  animation: earth linear 100s infinite;
}

@keyframes earth {
  from {
    transform: translatex(-50vh) scale(1.5) translatey(-203vh) scale(1.5);
  }

  to {
    transform: translatex(-25vh) scale(0) translatey(-260vh) scale(0);
  }
}

.circle1 {
  /*Pluto*/
  display: flex;
  margin-left: auto;
  margin-top: 1%;
  height: 10.37px;
  width: 10.37px;
  border-radius: 70px;
  background-color: antiquewhite rgb((255, 115, 0));
  /* For browsers that do not support gradients */
  background-image: radial-gradient(circle, rgb(150, 140, 131), rgb(0, 0, 0));
  position: relative;
}

.circle2 {
  /*Ganymede*/
  margin-left: 65%;
  margin-top: -20%;
