  width: 26px;
  border-radius: 50px;
  background-image: radial-gradient(circle, rgb(160, 157, 143), rgb(62, 63, 30));
  z-index: 1;
}

.circle3 {
  /*Mars*/
  margin-left: 10vh;
  margin-top: -85vh;
  position: fixed;
  height: 54px;
  width: 54px;
  border-radius: 50%;
  background:
    radial-gradient(95% 35% at 45% 1%, rgb(223, 208, 208), transparent 42%),
    radial-gradient(65% 65% at 25% 25%, rgb(90, 44, 7), transparent 53%),
    radial-gradient(100% 25% at 5% 0%, #351417, transparent 10%),
    radial-gradient(75% 15% at 55% 75%, #442702, transparent 50%),
    radial-gradient(75% 15% at 85% 70%, #000000, transparent 20%),
    radial-gradient(100% 15% at 35% 85%, #442702, transparent 25%),
    radial-gradient(100% 15% at 25% 75%, #442702, transparent 25%),
    radial-gradient(100% 5% at 15% 70%, #442702, transparent 25%),
    radial-gradient(95% 5% at 85% 88%, #442702, transparent 20%),
    radial-gradient(95% 10% at 55% 87%, #442702, transparent 30%),
    radial-gradient(100% 20% at 45% 100%, rgb(221, 218, 216), transparent 100%),
    radial-gradient(70% 55% at 5% 75%, rgb(97, 56, 9), transparent 63%),
    radial-gradient(100% 100% at 0% 5%, transparent, #b34125 98%),
    linear-gradient(20deg, #c44f32, #3a1317);
  box-shadow: inset -25px -5px 20px 1px rgba(128, 111, 56, 0.5);
  z-index: 0;
  animation: mars linear 45s infinite;
}

.rotatePlanets {
  transform: rotate(60deg);
  left: -36vw;
  top: 85vh;
}

@keyframes mars {
