.comet {
  /*comet*/
  position: fixed;
  margin-top: -90vh;
  left: 110vh;
  height: 200px;
  width: 0px;
  color: rgb(249, 252, 208);
  box-shadow: 0px 0px .5px 0.1px;
  border-radius: 500px 0px 0px 0px;
  transform-origin: 0 0;
  z-index: 0;
  animation: 20s comet linear infinite;
}

@keyframes comet {
  0% {
    opacity: 1;
    transform: scale(.6)rotate(-20deg);
  }

  1% {
    opacity: 1;
    transform: scale(3) rotate(-20deg);
  }

  2% {
    opacity: .8;
    transform: scale(4) rotate(-20deg);
  }

  3% {
    background-color: rgb(241, 190, 190);
    opacity: .6;
    transform: scale(5) rotate(-20deg);
  }

  4% {
    background-color: rgb(0, 0, 0);
    opacity: .5;
    transform: scale(6) rotate(-20deg);
  }

  5% {
    opacity: .3;
    transform: scale(7) rotate(-20deg);
  }

  10% {
    opacity: .0;
    transform: scale(7) rotate(-20deg);
  }

  100% {
    opacity: .0;
    transform: scale(7) rotate(-20deg);
  }
}

.circle15 {
  /*Sun*/
  position: fixed;
  right: 0;
  bottom: 0;
  height: 1000px;
  width: 1000px;
  clip-path: circle(200% at 100% 280%);
  background: rgb(235, 121, 68);
  background: linear-gradient(168deg, #ee5832 80%, #d87f02 87%, #fcd215 95%);
  z-index: 0;
}

