#logintop {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  height: 100px;
  width: 100px;
  border-top: 10px solid silver;
  border-bottom: 10px solid silver;
  border-left: 100px solid silver;
  border-right: 100px solid silver;
  padding: 12px;
  -webkit-animation: drop_in forwards 1.5s ease-in-out;
          animation: drop_in forwards 1.5s ease-in-out;
  box-shadow: 0px 0px 50px #2c4cee,
    inset 0 0 7px rgba(251, 253, 241, 0.712);
  font-size: 20px;
  background-image: linear-gradient(to right bottom, #5cc2c0, rgb(146, 246, 250));
  position: absolute;
  top: 0px;
  left: 30px;
  z-index: 50;
}

@-webkit-keyframes drop_in {
  from {
    opacity: 0;
    -webkit-transform: translateY(-50px)rotate(0deg);
            transform: translateY(-50px)rotate(0deg);
  }

  to {
    opacity: 1;
    -webkit-transform: translateY(100px)rotate(0deg);
            transform: translateY(100px)rotate(0deg);
  }
}

@keyframes drop_in {
  from {
    opacity: 0;
    -webkit-transform: translateY(-50px)rotate(0deg);
            transform: translateY(-50px)rotate(0deg);
  }

  to {
    opacity: 1;
    -webkit-transform: translateY(100px)rotate(0deg);
            transform: translateY(100px)rotate(0deg);
  }
}

#logintop::before {
  content: "";
  position: absolute;
  z-index: -1;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  height: 150px;
  width: 360px;
  -webkit-transform: translateY(-37px);
          transform: translateY(-37px);
  border-top: 10px solid rgb(8, 18, 109);
}

#logintop::after {
  content: "";
  left: auto;
  right: 10px;
  border: 10px solid rgb(249, 253, 6);
  border-radius: 50%;
  -webkit-animation: lightrotate 20s linear infinite;
          animation: lightrotate 20s linear infinite;
  -webkit-transform-origin: 0 0;
          transform-origin: 0 0;
}

@-webkit-keyframes lightrotate {
  0% {
    -webkit-transform: rotate(-.3rad)translate(-220px, -86px)scale(.2);
            transform: rotate(-.3rad)translate(-220px, -86px)scale(.2);
  }

  /* 20% {transform:scale(.3);}*/
  50% {
    -webkit-transform: rotate(.15rad)translate(-90px, -86px)scale(.5);
            transform: rotate(.15rad)translate(-90px, -86px)scale(.5);
  }

  /* 80% {transform:scale(.3);}  */
  100% {
    -webkit-transform: rotate(.6rad)translate(40px, -92px)scale(.2);
            transform: rotate(.6rad)translate(40px, -92px)scale(.2);
  }
}

@keyframes lightrotate {
  0% {
    -webkit-transform: rotate(-.3rad)translate(-220px, -86px)scale(.2);
            transform: rotate(-.3rad)translate(-220px, -86px)scale(.2);
  }

  /* 20% {transform:scale(.3);}*/
  50% {
    -webkit-transform: rotate(.15rad)translate(-90px, -86px)scale(.5);
            transform: rotate(.15rad)translate(-90px, -86px)scale(.5);
  }

  /* 80% {transform:scale(.3);}  */
  100% {
    -webkit-transform: rotate(.6rad)translate(40px, -92px)scale(.2);
            transform: rotate(.6rad)translate(40px, -92px)scale(.2);
  }
}

