/* picture boxes */

/* width */
.stylebox::-webkit-scrollbar {
  width: 10px;
}

/* Track */
.stylebox::-webkit-scrollbar-track {
  background: #000000;
  border-radius: 0px 0px 5px 5px;
  box-shadow: inset 0 0 7px #ceda8d;
}

/* Handle */
.stylebox::-webkit-scrollbar-thumb {
  background: linear-gradient(10deg, rgb(185, 106, 15) 10%, rgb(148, 140, 32) 100%);
  box-shadow: inset 0 0 5px 1px #929009;
  border-radius: 5px;
}

/* Handle on hover */
.stylebox::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(10deg, rgb(238, 198, 152) 30%, rgb(150, 146, 98) 100%);
  box-shadow: inset 0 0 5px 1px #929009;

}

.stylebox {
  display: flex;
  flex-basis: calc(100% / 3 - 34px);
  flex-wrap: nowrap;
  margin: 15px;
  border: solid 1px rgb(216, 86, 39);
  background-color: #0e1224;
  box-shadow: inset -2px 2px 5px #ceda8d,
    inset 2px -1px 5px #d4dd9f;
  min-width: 650px;
  max-height: 60vh;
  word-wrap: break-word;

  overflow: hidden;
}

