/*signup*/
.namepass {
  display: flex;
  flex-direction: column;
  transform: translate(5px, 5px);
}

.shell {
  width: 100vw;
  height: 100vh;
}

.icon_show {
  transform: translateX(-40px)
}

/* /////tourinfo page */

.tour-page {
  display: flex;
  flex-direction: row;
  max-width: fit-content;
}

.top {
  margin-top: 8px;
}

.pit-shell {
  flex-direction: column;
}

.venture-menu {
  width: fit-content;
  font-weight: bold;
  font-family: "Times New Roman", Times, serif;
}

.venture-menu a {
  background-color: rgb(0, 255, 42);
  color: blanchedalmond;
  display: block;
  padding: 8px;
  text-decoration: none;
  font-weight: bold;
  font-family: "Times New Roman", Times, serif;
}


Link a:hover {
  background-color: red;
}

.venture-menu a:hover {
  background-color: rgba(160, 13, 50, 0.5);
  font-weight: bold;
  font-family: "Times New Roman", Times, serif;
}

.venture-menu a:active {
  background-color: blanchedalmond;
  color: black;
  font-weight: bold;
  font-family: "Times New Roman", Times, serif;
}

.planet-image-text {
  display: flex;
  flex-basis: content;
  margin-top: 5px;
  margin-left: 10px;
}

.planet-image {
  display: flex;
  max-width: min-content;
  align-self: auto;
  padding: 4px;
  transform: scale(4);
  transform: scale(1);
}

.planet-text {
  display: flex;
  background-color: rgba(160, 13, 50, 0.25);
  color: white;
  flex-basis: 100%;
  margin: 5px;
  flex-flow: row wrap;
  border: solid 2px black;
  padding: 4px;
}

.top-border {
  background-color: darkolivegreen;
  width: 100%;
  height: 150px;
  background-image:
    radial-gradient(white, rgba(255, 255, 255, .1) 1px, transparent 20px);
  background-size: 550px 550px;
  background-position: 0px 0px;
}

/* APOD */


/* APOD Console buttons and action shadows */

.apodSee {
  display: flex;
  flex-direction: row;
  flex-basis: calc(100% / 3 - 34px);
  width: 75%;
  object-fit: contain;
  margin-top: -140px;
  margin-right: 1.2em;
}

.apodBox {
  display: flex;
  flex-direction: row;
  flex-basis: calc(100% / 3 - 34px);
  width: 100%;
  justify-content: space-between;
  margin-left: 15px;
}

.clear {
  height: 130px;
  width: 130px;
  opacity: 0;
  flex-wrap: nowrap;
  flex-direction: row;
}

.explanation {
  color: antiquewhite;
  font-weight: 500;
}

.apodButton {
  height: 120px;
  width: 100px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

/* picture boxes */

/* width */
.stylebox::-webkit-scrollbar {
  width: 10px;
}

/* Track */
.stylebox::-webkit-scrollbar-track {
  background: #000000;
  border-radius: 0px 0px 5px 5px;
  box-shadow: inset 0 0 7px #ceda8d;
}

/* Handle */
.stylebox::-webkit-scrollbar-thumb {
  background: linear-gradient(10deg, rgb(185, 106, 15) 10%, rgb(148, 140, 32) 100%);
  box-shadow: inset 0 0 5px 1px #929009;
  border-radius: 5px;
}

/* Handle on hover */
.stylebox::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(10deg, rgb(238, 198, 152) 30%, rgb(150, 146, 98) 100%);
  box-shadow: inset 0 0 5px 1px #929009;

}

.stylebox {
  display: flex;
  flex-basis: calc(100% / 3 - 34px);
  flex-wrap: nowrap;
  margin: 15px;
  border: solid 1px rgb(216, 86, 39);
  background-color: #0e1224;
  box-shadow: inset -2px 2px 5px #ceda8d,
    inset 2px -1px 5px #d4dd9f;
  min-width: 650px;
  max-height: 60vh;
  word-wrap: break-word;

  overflow: hidden;
}

.infobox {
  display: flex;
  flex-direction: column;
  width: 95%;
  padding: 5px;
  object-fit: contain;
  overflow: auto;
  margin-top: 30px;
}

.apodPhoto {
  display: flex;
  flex-direction: column;
  flex-basis: calc(100% / 3 - 34px);
  display: block;
  margin: auto;
  max-height: 100%;

}

.apodPhoto img {

  width: 95%;
  margin: 10px;
  object-fit: contain;
  max-height: 70vh;
}


.apodPhoto h1 {
  margin-top: 10px;
  margin-bottom: 0;
}


.urla {
  margin-top: 0;
  overflow: visible;
}

.apodPhoto .date {
  margin-top: 0;
  margin-bottom: 10px;
}

.apodPhoto .explanation {
  color: #bbb;
  margin: 15px;
}

@media (max-width: 600px) {
  .apodPhoto {
    display: block;
  }

  .infobox .apodPhoto img {
    width: 100%;
    margin-right: 0;
    margin-bottom: 5px;
  }
}

/* Album Like button */

.like-btn {
  margin: 10px;
  border-radius: 10px;
  border: 2px rgb(25, 200, 212);
  background-color: #101652;
  color: #bff3bf;
  width: 146px;
  display: block;
  margin: 10px auto 0px auto;
  padding: 8px 18px 8px 18px;
  box-shadow: inset 0px 1px 3px #388bf7;
  text-shadow: -1px -1px 0 #000, 
                1px -1px 0 #000, 
                -1px 1px 0 #000, 
                1px 1px 0 #000;
}

.like-btn :hover {
  background-color: #04529b;

}

.like-btn::before:hover {
  width: 100px;
  height: 20px;
  background-color: #0ff;
}

/* header buttons */

.red {
  background-color: rgb(150, 131, 131);
  transition: 3.3s ease all;
  outline: 2px solid white;


}

.red::hover {
  background-color: rgb(128, 6, 6);
  outline: 2ps solid red;
  outline-offset: 11px;
  transition: outlineOffset 250ms ease;
}


