/*  IPN */
.ipn {
  margin: 5em;
  height: 100px;
  width: 100px;
  border-radius: 50px;
  box-shadow: inset 0 0 18px #fff,
    inset 6px 0 18px #f3bad6,
    inset -6px 0 18px #0ff,
    inset 6px 0 30px #f3bad6,
    inset -6px 0 30px #0ff,
    0 0 18px #fff, -4px 0 18px #f3bad6, 4px 0 18px #0ff;
  animation: rotation 1.5s infinite linear;
  background: repeating-radial-gradient(#030302, hsl(221, 47%, 46%), hsl(330, 59%, 20%) 8px);
}

@keyframes rotation {
  from {
    transform: scale(.01) rotate(0deg);
  }

  to {
    transform: scale(1.5) rotate(1100deg);
  }
}

